<template>
    <div class="order">
        <div class="order__price">实付金额 &yen;{{calculations}}</div>
        <div class="order__btn" @click="()=>handleSubmitClick(true)">提交订单</div>
    </div>
    <div 
        class="mask" 
        v-show="showConfirm"
        @click="handleSubmitClick(false)"
    >
        <div class="mask__content" @click.stop>
            <h3 class="mask__content__title">确认离开收银台?</h3>
            <p class=" mask__content__desc">请尽快完成支付，否则将被取消</p>
            <div class="mask__content__btns">
                <div 
                    class="mask__content__btn mask__content__btn--first"
                    @click="()=> handleConfirmOrder(true)"
                >取消订单</div>
                <div 
                    class="mask__content__btn mask__content__btn--last"
                    @click="()=> handleConfirmOrder(false)"
                >确认支付</div>
            </div>
        </div>
    </div>
</template>
<script>
import { ref } from 'vue'
import { useRoute, useRouter } from 'vue-router'
import { useStore } from 'vuex'
import { post } from '../../utils/request';
import { useCommonCartEffect } from '../../effects/cartEffectss'

//下单
const useMakeOrderEffect=(shopId,shopName,productList)=>{

    console.log(productList)

    //数据库实例
    const store = useStore()

    //页面跳转实例
    const router = useRouter()

    //确认事件(向axios传送信息)
    const handleConfirmOrder = async(isCanceled)=>{

        //创建一个数组存入商品id,数量
        const products = []
        //方法
        for(let i in productList.value){
            const product = productList.value[i]
            products.push({
                id: parseInt(product._id),
                num: product.count
            })
        }
        //数组
        // const products = productList.value.map(item=>({
        //     id: item._id,
        //     num: item.count
        // }))
        console.log(products)
        try{
            console.log('进来吧');
            //axios的请求
            const result = await post('/api/order',{
                addressId: 1,
                shopId,
                shopName: shopName.value,
                isCanceled,
                products
            })
            console.log(result);
            if( result.errno === 0){
                //清空数据库
                store.commit('clearCartData',shopId)
                //页面跳转
                router.push({ name:'OrderList'})
            }
        }
        catch(e){
            //提示下单失败
            // showToast('请求失败')

        }
    }
    return{ handleConfirmOrder }
}

//蒙层展示
const useShowMaskeEffect=()=>{

    //定义页面是否显示变量
    const showConfirm = ref(false)

     //点击提交订单事件
    const handleSubmitClick=(status)=>{
        showConfirm.value = status 
    }

    return { showConfirm, handleSubmitClick }
}



export default {
    name:'Order',
    setup(){
        
        //页面跳转实例化(获取数据)
        const route = useRoute()

        //获取商店页码
        const shopId = parseInt(route.params.id, 10)

        //调用外部js处理数据，返回对应返回值
        const { shopName, calculations, productList } = useCommonCartEffect(shopId)

        console.log(productList)

        //调用内部封装(下单逻辑)
        const { handleConfirmOrder } = useMakeOrderEffect(shopId,shopName,productList)

        //调用内部封装(蒙层展示)
        const { showConfirm, handleSubmitClick } = useShowMaskeEffect()

       
        return { handleConfirmOrder, calculations, showConfirm, handleSubmitClick }
    }
}
</script>
<style lang="scss" scoped>
.order{
    position: absolute;
    left:0;
    right:0;
    bottom:0;
    display: flex;
    height: .49rem;
    line-height: .49rem;
    background:#fff ;
    &__price{
        flex:1;
        text-indent: .24rem;
        font-size:.14rem;
        font-weight: bold;
        color:#333;
    }
    &__btn{
        width: .98rem;
        background: #3fb0f9;
        color:#fff;
        text-align: center;
        font-size: .14rem;
    }
}
.mask{
    z-index: 1;
    position:absolute;
    left:0;
    right:0;
    bottom:0;
    top:0;
    background: rgba(0,0,0,0.50);
    &__content{
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%,-50%);
        width: 3rem;
        height: 1.56rem;
        background:#fff;
        border-radius: .04rem;
        text-align: center;
        &__title{
            margin: .24rem 0 0 0;
            font-size: .18rem;
            color: #333;
        }
        &__desc{
            margin: .08rem 0 0 0;
            font-size: .14rem;
            color:#666;
        }
        &__btns{
            display: flex;
            margin: .24rem .58rem ;
        }
        &__btn{
            flex: 1;
            width: .8rem;
            line-height: .32rem;
            border-radius: 16rem;
            font-size: .14rem;
            &--first{
                margin-right: .12rem;
                border: .01rem solid #4fb0f9;
                color: #4fb0f9;
            }
            &--last{
                margin-left: .12rem;
                background: #4fb0f9;
                color:#fff;
            }
        }
    }
}
</style>