<template>
   <div class="wrapper">
      <div class="title">我的订单</div>
      <div class="orders">
         <div 
            class="order" 
            v-for="(item,index) in list"
            :key="index"
         >
            <div class="order__title">
               {{item.shopName}}
               <span class="order__status">
                  {{item.isCanceled ? '已取消' : '已下单'}}
               </span>
            </div>
            <div class="order__content">
               <div class="order__content__imgs">
                  <template
                     v-for="(innerItem, innerIndex) in item.products"
                     :key="innerIndex"
                  >
                     <img 
                        class="order__content__img" 
                        :src="innerItem.products.img" 
                        v-if="innerIndex<= 3"
                     >
                  </template>
               </div>
               <div class="order__content__info">
                  <div class="order__content__price">&yen;{{item.totalPirce}}</div>
                  <div class="order__content__count">共{{item.totalNumber}}件</div>
               </div>
            </div>
         </div>
      </div>
   </div>
   <Docker :crurentIndex="2"/>
</template>
<script>
import { reactive,toRefs } from 'vue'
import Docker from '../../components/Docker'
import { get } from '../../utils/request'

//处理订单列表逻辑
const useOrderListEffect =()=>{

   //定义接取数据变量
   const data = reactive({ list:[] })

   //封装获取数据(从axios获取数据)
   const getnearbyList = async ()=>{
      //axios的请求-从utils的get方法里
      const result = await get('/api/order')
      //判断数据库如果errno=0而且有数据
      // console.log(result);
      if (result?.errno === 0 && result?.data?.length){

         //获取数据库数据
         const orderList= result.data

         console.log(orderList)

         //数据循环，获取物品总价
         orderList.forEach((order)=>{
            const products = order.products || []
            // console.log(products)
            //定义总价变量
            let totalPirce = 0
            //定义总数变量
            let totalNumber = 0

            products.forEach((productItem)=>{
               totalNumber += ( productItem?.orderSates || 0 )
               totalPirce += ( productItem?.products?.price * productItem.orderSates || 0 )//ordersltes
            })
            order.totalPirce = totalPirce
            order.totalNumber = totalNumber
         })
         console.log(orderList)

         // console.log(result);
         // console.log(result.data);
         data.list = result.data
      } 
  }
   // console.log(data);

   getnearbyList()
   const { list } = toRefs(data)

   console.log(list,'--list--')

   return { list }
}
export default {
    name:'OrderList',
    components:{ Docker },
    setup(){
       const { list } = useOrderListEffect()
       console.log( list);

       return{ list }
    }
}
</script>
<style lang="scss" scoped>
@import '../../style/viriables.scss';
.wrapper{
    overflow-y: auto;
    position: absolute;
    left:0;
    top:0;
    bottom: .5rem;
    right:0;
    background:rgb(248,248,248);
}
.title{
   line-height:.44rem;
   background:#fff;
   color:$content-fontcolor;
   font-size: .14rem;
   text-align: center;
}
.order{
   margin: .16rem .18rem;
   padding: .16rem;
   background:#fff;
   &__title{
      margin-bottom: .16rem;
      line-height:.22rem;
      font-size: .16rem;
      color:$content-fontcolor;
   }
   &__status{
      float: right;
      font-size:.14rem;
      color:$light-fontColor;
   }
   &__content{
      display:flex;
      &__imgs{
         flex: 1;
      }
      &__img{
         width: .4rem;
         height: .4rem;
         margin-right: .17rem;
      }
      &__info{
         width: .7rem;
         text-align: right;
      }
      &__price{
         margin-bottom: .04rem;
         line-height: .2rem;
         font-size: .14rem;
         color:$hightlight-fontColor;
      }
      &__count{
         line-height: .14rem;
         font-size: .12rem;
         color:$content-fontcolor;
      }
   }
}
</style>